.box {
  display: inline-block;
  position: relative;
  background-color: #00586e8a;
  cursor: grab;
}

.draggable-area {
  width: 95%;
  position: absolute;
  height: 95%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  user-select: none;
}

.box.target {
  background-color: #24945c;
}

.box .resizers {
  width: 100%;
  height: 100%;
  border: 0.5px solid #4286f4;
  box-sizing: border-box;
  user-select: none;
}

.box .resizers .resizer {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: white;
  border: 1px solid #4286f4;
  position: absolute;
}

.box .resizers .resizer.top-left {
  left: -2.5px;
  top: -2.5px;
  cursor: nwse-resize;
}

.box .resizers .resizer.top-right {
  right: -2.5px;
  top: -2.5px;
  cursor: nesw-resize;
}

.box .resizers .resizer.bottom-left {
  left: -2.5px;
  bottom: -2.5px;
  cursor: nesw-resize;
}

.box .resizers .resizer.bottom-right {
  right: -2.5px;
  bottom: -2.5px;
  cursor: nwse-resize;
}

.name {
  position: absolute;
  width: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  user-select: none;
}

